import { Component, OnInit } from '@angular/core';
@Component({
  selector: 'app-me',
  templateUrl: './me.component.html',
  styleUrls: ['./me.component.css']
})
export class MeComponent implements OnInit {

  constructor() { }

  ngOnInit() {

  }
  chartOptions = {
    responsive: true 
  }
  labels =  ['HTML5', 'CSS3', 'JavaScript', 'Tabletennis', 'Angular', 'Bootstrap','cooking'];
  // STATIC DATA FOR THE CHART IN JSON FORMAT.
  chartData = [
    {
      label: 'Skills',
      data: [90, 85, 80, 72, 75, 80, 39] 
    }
  ];
  colors = [
    {
      backgroundColor:["red","blue","green","yellow","gray","pink", "aqua"],
    }
  ]
  // CHART CLICK EVENT.
  onChartClick(event) {
    console.log(event);
  }


 
}



